.project-title {
  min-height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #53b3cb;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 16px auto 48px auto;
  max-width: 800px;
  text-align: center;
}

.activity-suggestion {
  color: #53b3cb;
  display: flex;
  flex-direction: column;
  text-align: center;
  font-size: 1.2rem;
}

.avatar-container {
  align-self: center;
  justify-self: center;
}

.intro-container,
.intro-container > * {
  color: #53b3cb;
}

.avatar-img {
  border-radius: 100%;
  width: 150px;
  border: 2px solid #53b3cb;
}

body {
  background-color: #0c090d;
}

.project-poster {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80%;
  max-width: 500px;
  height: 150px;
  margin: 16px auto;

  border: 0.2rem solid #fff;
  border-radius: 2rem;
  padding: 0.4em;
  box-shadow: 0 0 0.2rem #fff, 0 0 0.2rem #fff, 0 0 2rem #f15946,
    0 0 0.8rem #f15946, 0 0 2.8rem #f15946, inset 0 0 1.3rem #f15946;
}

@media (max-width: 650px) {
  .container {
    margin: 8px 8px;
  }
}
